import './MoviesList.scss';
import { Movie } from '../../types/Movie';
import { MovieCard } from '../MovieCard';

interface MoviesArray {
  movies: Movie[];
}

export const MoviesList = (movies: MoviesArray) => (
  <div className="movies">
    {movies.movies.map(
      movie => {
        return (
          <MovieCard {...movie} key={movie.imdbId} />
        );
      },
    )}
  </div>
);
